<template>
    <div class="money">
        <a-breadcrumb class="bread">
            <a-breadcrumb-item>
                <a href="/customerService/pc">{{ $t("language.service") }}</a>
            </a-breadcrumb-item>
            <a-breadcrumb-item class="text">
                {{ $t("language.application") }}
            </a-breadcrumb-item>
        </a-breadcrumb>
        <div class="content">
            <div class="tabs">
                <div
                    class="item"
                    :class="{ Selected: tabIndex == 1 }"
                    @click="onChangeTabs(1)"
                >
                    {{ $t("language.application") }}
                </div>
                <div
                    class="item"
                    :class="{ Selected: tabIndex == 2 }"
                    @click="onChangeTabs(2)"
                >
                    {{ $t("language.record") }}
                </div>
            </div>
            <div class="card">
                <template v-if="tabIndex == 1">
                    <div>
                        <a-row class="rowItem">
                            <a-col :span="8">
                                <p class="lable">
                                    {{ $t("language.memberaccount") }}
                                </p>
                            </a-col>
                            <a-col :span="12">
                                <a-input
                                    v-model="formState.username"
                                    :placeholder="$t('language.account')"
                                />
                            </a-col>
                        </a-row>
                        <a-row class="rowItem">
                            <a-col :span="8">
                                <p class="lable">{{ $t("language.name") }}</p>
                            </a-col>
                            <a-col :span="12">
                                <a-input
                                    v-model="formState.name"
                                    :placeholder="$t('language.nameInput')"
                                />
                            </a-col>
                        </a-row>
                        <a-row class="rowItem">
                            <a-col :span="8">
                                <p class="lable">{{ $t("language.money") }}</p>
                            </a-col>
                            <a-col :span="12">
                                <a-input
                                    v-model="formState.money"
                                    type="number"
                                    :placeholder="$t('language.depositamount')"
                                />
                            </a-col>
                        </a-row>
                        <a-row class="rowItem">
                            <a-col :span="8">
                                <p class="lable">
                                    {{ $t("language.screenshot") }}
                                </p>
                            </a-col>
                            <a-col :span="12">
                                <a-upload
                                    list-type="picture-card"
                                    :customRequest="imgAdd"
                                    :remove="imgDel"
                                    @preview="handlePreview"
                                    v-model="fileList"
                                    :disabled="disabledImg"
                                >
                                    <div v-if="fileList.length < 1">
                                        <a-icon type="plus" />
                                        <div class="ant-upload-text">
                                            {{ $t("language.upload") }}
                                        </div>
                                    </div>
                                </a-upload>
                                <a-modal
                                    :visible="previewVisible"
                                    :footer="null"
                                    @cancel="handleCancel"
                                >
                                    <img
                                        alt="example"
                                        style="width: 100%"
                                        :src="previewImage"
                                    />
                                </a-modal>
                            </a-col>
                        </a-row>
                        <a-row class="rowItem">
                            <a-col :span="8"> </a-col>
                            <a-col :span="12" class="btnCol">
                                <div>
                                    <p class="tips">
                                        {{ $t("language.tipText") }}
                                    </p>
                                    <p class="tips">
                                        {{ $t("language.text7") }}
                                    </p>
                                </div>
                                <a-button
                                    type="primary"
                                    class="btnNext"
                                    @click="onSubmit"
                                >
                                    {{ $t("language.submitnow") }}
                                </a-button>
                            </a-col>
                        </a-row>
                    </div>
                </template>
                <template v-if="tabIndex == 2">
                    <div>
                        <a-table
                            :columns="columns"
                            :data-source="data"
                            bordered
                            :pagination="{
                                pageSize: 5,
                                hideOnSinglePage: true,
                            }"
                        >
                            <span slot="createdAt" slot-scope="text">
                                <span>
                                    {{ $moment(text).format("YYYY-MM-DD") }}
                                </span>
                            </span>
                            <span slot="status" slot-scope="text">
                                <span v-if="text == 2">{{
                                    $t("language.success")
                                }}</span>
                                <span v-if="text == 3">{{
                                    $t("language.failure")
                                }}</span>
                                <span v-if="text == 1" class="processing">{{
                                    $t("language.Processing")
                                }}</span>
                            </span>
                        </a-table>
                    </div>
                </template>
            </div>
        </div>
    </div>
</template>

<script>
import uploadImg from "../utils/uploadimg.js";
import { Modal } from "ant-design-vue";
export default {
    data() {
        return {
            formState: {},
            tabIndex: 1,
            previewVisible: false,
            previewImage: "",
            imgUrl: "",
            fileList: [],
            columns: [],
            data: [],
            subimgBtn: true,
            disabledImg: false,
        };
    },
    created() {
        this.columns = [
            {
                title: this.$t("language.application1"),
                align: "center",
                dataIndex: "createdAt",
                scopedSlots: {
                    customRender: "createdAt",
                },
            },
            {
                title: this.$t("language.actualname"),
                align: "center",
                dataIndex: "memeberName",
            },
            {
                title: this.$t("language.Amount"),
                align: "center",
                dataIndex: "amount",
            },
            {
                title: this.$t("language.status"),
                align: "center",
                dataIndex: "status",
                scopedSlots: {
                    customRender: "status",
                },
            },
            {
                title: this.$t("language.remarks"),
                align: "center",
                dataIndex: "memo",
                ellipsis: true,
            },
        ];
        this.formState.username = this.$config.username;
    },
    methods: {
        onChangeTabs(i) {
            if (i == 2 && !this.$config.username) {
                Modal.error({
                    title: this.$t("language.tips"),
                    content: this.$t("language.login"),
                });
                return;
            }
            this.formState = {};
            this.formState.username = this.$config.username;
            this.fileList = [];
            this.tabIndex = i;
            this.getSetMoneyList();
        },
        imgAdd(data) {
            let formdata = new FormData();
            formdata.append("file", data.file);
            // console.log(data, formdata);
            this.disabledImg = true;
            uploadImg(this.$api.uploadImg, formdata).then((res) => {
                console.log("上传图片", res);
                this.disabledImg = false;
                if (res.code == 0) {
                    this.imgUrl = res.data.url;
                    this.fileList.push(this.imgUrl);
                    data.onSuccess(res.data);
                }
            });
        },
        handlePreview(e) {
            console.log(e);
            this.previewVisible = true;
            this.previewImage = e.thumbUrl;
        },
        handleChange() {},
        imgDel(i) {
            console.log("删除", i);
            this.fileList = [];
        },
        handleCancel() {
            this.previewVisible = false;
        },
        getSetMoneyList() {
            let data = {
                memeberName: this.$config.username,
            };
            this.$http.post(this.$api.getSetMoneyList, data).then((res) => {
                if (res.code == 0) {
                    console.log(res);
                    this.data = res.data;
                }
            });
        },
        showModel() {
            let _this = this;
            const model = Modal.info({
                title: "提示",
                content: this.$t("language.tipssueecc"),
                centered: true,
                okText: this.$t("language.seePage"),
                icon: "none",
                class: "modelSueecc",
                closable: true,
                maskClosable: true,
                onOk() {
                    return new Promise((resolve, reject) => {
                        _this.onChangeTabs(2);
                        resolve();
                    });
                },
                onCancel() {
                    _this.$router.go(-1);
                    console.log("关闭----------");
                },
            });
        },
        onSubmit() {
            if (!this.subimgBtn) {
                return;
            }
            if (!this.formState.username) {
                this.$message.warning(this.$t("language.account"));
                return;
            }
            if (!this.formState.money) {
                this.$message.warning(this.$t("language.depositamount"));
                return;
            }
            if (!this.formState.name) {
                this.$message.warning(this.$t("language.nameInput"));
                return;
            }
            if (!this.imgUrl) {
                this.$message.warning(this.$t("language.imgUpload"));
                return;
            }
            let parames = {
                memeberName: this.formState.username,
                realName: this.formState.name,
                amount: this.formState.money,
                annex: this.imgUrl,
            };
            if (this.$config.clientItem == "aygj") {
                if (!this.$config.username) {
                    parames.memeberName = parames.memeberName + "ay";
                } else {
                    var disLength = parames.memeberName.length;
                    var shortName = parames.memeberName.substring(
                        disLength - 2,
                        disLength
                    );
                    if (shortName != "ay") {
                        parames.memeberName = parames.memeberName + "ay";
                    }
                }
            }
            console.log(parames);
            this.subimgBtn = !this.subimgBtn;
            this.$http.post(this.$api.addSetMoney, parames).then((res) => {
                this.subimgBtn = !this.subimgBtn;
                if (res.code == 0) {
                    console.log(res);
                    this.showModel();
                    // this.$message.success(this.$t("language.successfully"));
                    // this.$router.go(-1);
                }
            });
        },
    },
};
</script>

<style lang="scss" >
.modelSueecc {
    text-align: center;
    .ant-modal-body {
        text-align: center;
    }
    .ant-modal-confirm-title {
        color: #333;
        font-size: 18px;
    }
    .ant-modal-confirm-content {
        display: flex;
        align-items: center;
        justify-content: center;
        min-height: 100px;
    }
    .ant-modal-confirm-btns {
        float: none;
        .ant-btn-primary {
            background: var(--themeColor) !important;
            width: 40% !important;
            border: none;
        }
    }
}
.money {
    margin: 10px 18%;
    .content {
        margin: 20px 15%;
        .tabs {
            display: flex;
            .item {
                background: #fff;
                color: #bcbcbc;
                font-size: 16px;
                padding: 16px 27px;
                cursor: pointer;
                border-bottom: 1px solid #e4e4e4;
            }
            .Selected {
                color: var(--themeColor);
                border-bottom: 2px solid var(--themeColor);
            }
        }
        .card {
            background: #fff;
            padding: 30px;
            .rowItem {
                margin-bottom: 20px;
                .btnCol {
                    text-align: left;
                    .btnNext {
                        background: var(--themeColor);
                        border: none;
                        padding-left: 35px;
                        padding-right: 35px;
                    }
                    .tips {
                        color: var(--themeColor);
                    }
                }
                .flexBox {
                    display: flex;
                    .btnCode {
                        margin-left: 10px;
                        background: var(--themeColor);
                        border: none;
                    }
                }
                .lable {
                    margin: 0;
                    text-align: right;
                    line-height: 44px;
                    padding-right: 10px;
                    color: #333;
                    font-size: 16px;
                }
            }
        }
    }
    .processing {
        color: #f9a425;
    }
}
</style>